.ReactCrop {
	position: relative;
	display: inline-block;
	cursor: crosshair;
	overflow: hidden;
	max-width: 100%;
}
.ReactCrop:focus {
	outline: none;
}
.ReactCrop--disabled,
.ReactCrop--locked {
	cursor: inherit;
}
.ReactCrop__image {
	display: block;
	max-width: 100%;
	touch-action: manipulation;
}
.ReactCrop--crop-invisible .ReactCrop__image {
	opacity: 0.5;
}
.ReactCrop__crop-selection {
	position: absolute;
	top: 0;
	left: 0;
	transform: translate3d(0, 0, 0);
	box-sizing: border-box;
	cursor: move;
	box-shadow: 0 0 0 9999em rgba(0, 0, 0, 0.5);
	touch-action: manipulation;
	border: 1px solid;
	border-image-source: url('data:image/gif;base64,');
	border-image-slice: 1;
	border-image-repeat: repeat;
}
.ReactCrop--disabled .ReactCrop__crop-selection {
	cursor: inherit;
}

.ReactCrop__rule-of-thirds-vt::before,
.ReactCrop__rule-of-thirds-vt::after,
.ReactCrop__rule-of-thirds-hz::before,
.ReactCrop__rule-of-thirds-hz::after {
	content: '';
	display: block;
	position: absolute;
	background-color: rgba(255, 255, 255, 0.4);
}
.ReactCrop__rule-of-thirds-vt::before,
.ReactCrop__rule-of-thirds-vt::after {
	width: 1px;
	top: 0;
	bottom: 0;
}
.ReactCrop__rule-of-thirds-vt::before {
	left: 33.3333%;
	left: calc(100% / 3);
}
.ReactCrop__rule-of-thirds-vt::after {
	left: 66.6666%;
	left: calc(100% / 3 * 2);
}
.ReactCrop__rule-of-thirds-hz::before,
.ReactCrop__rule-of-thirds-hz::after {
	left: 0;
	right: 0;
	height: 1px;
}
.ReactCrop__rule-of-thirds-hz::before {
	top: 33.3333%;
	top: calc(100% / 3);
}
.ReactCrop__rule-of-thirds-hz::after {
	top: 66.6666%;
	top: calc(100% / 3 * 2);
}
.ReactCrop__drag-handle {
	position: absolute;
	width: 10px;
	height: 10px;
	background-color: rgba(0, 0, 0, 0.2);
	border: 1px solid rgba(255, 255, 255, 0.7);
	box-sizing: border-box;
	outline: 1px solid transparent;
}
.ReactCrop .ord-nw {
	top: 0;
	left: 0;
	margin-top: -5px;
	margin-left: -5px;
	cursor: nw-resize;
}
.ReactCrop .ord-n {
	top: 0;
	left: 50%;
	margin-top: -5px;
	margin-left: -5px;
	cursor: n-resize;
}
.ReactCrop .ord-ne {
	top: 0;
	right: 0;
	margin-top: -5px;
	margin-right: -5px;
	cursor: ne-resize;
}
.ReactCrop .ord-e {
	top: 50%;
	right: 0;
	margin-top: -5px;
	margin-right: -5px;
	cursor: e-resize;
}
.ReactCrop .ord-se {
	bottom: 0;
	right: 0;
	margin-bottom: -5px;
	margin-right: -5px;
	cursor: se-resize;
}
.ReactCrop .ord-s {
	bottom: 0;
	left: 50%;
	margin-bottom: -5px;
	margin-left: -5px;
	cursor: s-resize;
}
.ReactCrop .ord-sw {
	bottom: 0;
	left: 0;
	margin-bottom: -5px;
	margin-left: -5px;
	cursor: sw-resize;
}
.ReactCrop .ord-w {
	top: 50%;
	left: 0;
	margin-top: -5px;
	margin-left: -5px;
	cursor: w-resize;
}
.ReactCrop__disabled .ReactCrop__drag-handle {
	cursor: inherit;
}
.ReactCrop__drag-bar {
	position: absolute;
}
.ReactCrop__drag-bar.ord-n {
	top: 0;
	left: 0;
	width: 100%;
	height: 6px;
	margin-top: -3px;
}
.ReactCrop__drag-bar.ord-e {
	right: 0;
	top: 0;
	width: 6px;
	height: 100%;
	margin-right: -3px;
}
.ReactCrop__drag-bar.ord-s {
	bottom: 0;
	left: 0;
	width: 100%;
	height: 6px;
	margin-bottom: -3px;
}
.ReactCrop__drag-bar.ord-w {
	top: 0;
	left: 0;
	width: 6px;
	height: 100%;
	margin-left: -3px;
}

@media (pointer: coarse) {
	.ReactCrop__drag-handle {
		width: 34px;
		height: 34px;
	}
	.ReactCrop__drag-bar {
		display: none;
	}
	.ReactCrop .ord-nw,
	.ReactCrop .ord-n,
	.ReactCrop .ord-ne,
	.ReactCrop .ord-e,
	.ReactCrop .ord-s,
	.ReactCrop .ord-sw,
	.ReactCrop .ord-w {
		display: none;
	}
	.ReactCrop .ord-se {
		margin-bottom: -1px;
		margin-right: -1px;
	}
}
